
.rapidhome{
		height: calc(100% - 80px);
		overflow: hidden;
}
.rapidhome-content{
		height:100%;
}
.rapidhome-content{
		margin-top:80px;
		font-size: 20px;
		padding: 30px 5%;
}
.rapid-item{
		display: inline-block;
		width: 20%;
		line-height: 1.2rem;
		text-align: center;
		margin:0 2.5%;
		border: 1px solid #fc9452;
		color: #fc9452;
		font-size: .44rem;
		-webkit-box-sizing: border-box;
		        box-sizing: border-box;
		margin-bottom: .5rem;
		border-radius: .12rem;
}
.rapid-item.on{
		background:#fc9452 ;
		color: #fff;
}
.rapidhome-bottom{
		text-align: center;
		position: fixed;
    width: 100%;
    bottom: 30px;
    left: 0;
}
.rapidhome-btn{
		display: inline-block;
		background: #3c9bff;
    color: #fff;
    width: 26%;
    text-align: center;
    font-size: .48rem;
    line-height: 1rem;
    border-radius: 12px;
}

*[data-v-67359457]{
		margin: 0;
		padding: 0;
}
.titleline[data-v-67359457]{
		width: 100%;
		height: 80px;
		font-size: 1.5vw;
    position: fixed;
    top: 0;
    z-index: 9;
    text-align: center;
    line-height: 80px;
    background: #fff;
}
	
